@import url(https://fonts.googleapis.com/css?family=Raleway|Reenie+Beanie);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}
body{
  padding-top: 20px;
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.footer {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  color: white;
  text-align: center;
  padding: 10px;
  padding-Top: 30px;
}

.search_field{
  width: 80%;
  margin-top: 30px;
  height: 100%;
  background-color: rgb(67, 197, 214);
  margin-left: auto;
  margin-right: auto;
}

.search_icon{
  cursor: pointer;
}

.box{
  height: 70px;
}
* {
  box-sizing: border-box;
}

img {
  width: 100%;
  height: 70%;
}

a {
  color: black;
}

.caption,
h1,
.date {
  margin: 0;
  text-align: center;
  font-size: 30px;
}

.caption,
h1 {
  font-family: "Reenie Beanie", sans-serif;
  font-size: 35px;
  padding: 10px 10px 0 10px;
  margin-top: 10px;
}

.date {
  font-size: 1em;
}

.description {
  font-size: 0.9em;
  padding: 0 20px 20px 20px;
  text-align: left;
}

.container {
  perspective: 1000px;
  display: inline-block;
  margin: 3%;
  overflow-wrap: break-word;
}

.container:hover .flipper {
  transform: rotateY(180deg);
}

.container,
.front,
.back {
  width: 300px;
  height: 350px;
}


.flipper {
  transition: 1s;
  transform-style: preserve-3d;
  position: relative;
  
}

.front,
.back {
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  position: absolute;
  top: 0;
  left: 0;
  width: 300px;
  height: 350px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.front {
  z-index: 2;
}

.back {
  transform: rotateY(180deg);
}

p{
  color: black;
  font-weight: 500;
}


.pagination{
    margin-left: auto;
    margin-right: auto;
}
@media(max-width:600px){
    .pagination{
        overflow-x: scroll;
    }
}


